import React, { Component } from 'react';
import { ProductConsumer } from '../context';
import { Link } from 'react-router-dom';
import { ButtonContainer } from './Button';

export default class Details extends Component {
    render() {
        return (
            <ProductConsumer>
                {value=>{
                    const {id,company,img,info,price,title,inCart} = value.detailProduct;
                    return (
                        <div className="container py-2">
                            {/* Titulo */}
                            <div className="row">
                                <div className="col-10 mx-auto text-center text-slanted text-blue my-2">
                                    <h1>{title}</h1>
                                </div>
                            </div>
                            {/* end Titulo */}
                            {/* Product Info */}
                            <div className="row">
                                {/* Product IMG */}
                                <div className="col-10 mx-auto col-md-6 my-3">
                                    <img src={value.imgThumbnailM(img)} className="img-fluid rounded mx-auto d-block" alt="product" />    
                                </div>
                                {/* Product text */}
                                <div className="col-10 mx-auto col-md-6 my-3 text-capitalize">
                                    {/*<h2>model : {title}</h2>
                                    <h4 className="text-title text-uppercase text-muted mt-3 mb-2"> made by : <span className="text-uppercase"></span>{company}</h4>
                                    <h4 className="text-blue"><strong>price : <span>$</span>{price}</strong></h4>*/}
                                    <p className="text-capitalize font-weight-bold mt-3 mb-0"> informacion sobre el producto</p>
                                    <p className="text-muted lead multiline">{info}</p>
                                    <div>
                                        <Link to='/'>
                                            <ButtonContainer>Galeria</ButtonContainer>
                                        </Link>
                                        <ButtonContainer cart
                                            disabled={inCart?true:false}
                                            onClick={()=>{
                                                value.addToCart(id);
                                                value.openModal(id);
                                            }}
                                            >
                                            {inCart? "In Cart": "Add to Cart"}
                                        </ButtonContainer>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }}
            </ProductConsumer>
        )
    }
}